body {
	background: #fff;
}

body {
	font-family: arial, helvetica, sans-serif;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td, th {
	padding: .52rem;
	margin: 0;
}

thead th {
	border-bottom: 1px solid #000;
}

tr:nth-child(even) td {
	background: #f2f2f2;
}




@import 'styles.css';



